@import '../variables/27';


@font-face {
    font-family: 'sharpsansbook';
    src: url('#{$tocc-site-fonts-dir}/sharpsansbook/SharpSans-Book.woff2') format('woff2'),
         url('#{$tocc-site-fonts-dir}/sharpsansbook/SharpSans-Book.woff') format('woff'),
         url('#{$tocc-site-fonts-dir}/sharpsansbook/SharpSans-Book.eot') format('eot');
    font-weight: normal;
    font-style: normal;
}
